* {
  box-sizing: border-box;
}
main {
  margin-left: 10%;
  width: 90%;
  height: 91vh;
}

#createTrip {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#home-container {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0.5fr 1.5fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    "title"
    "trips-container";
}

.title {
  justify-self: center;
  align-self: center;
  grid-area: title;
}

.trips-container {
  grid-area: trips-container;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
  gap: 2rem;
  padding-bottom: 40px;
}
